'use strict';

/**
* Symbol used to determine if a constructor object recognizes an object as its instance.
*
* @module @stdlib/symbol-has-instance
*
* @example
* var isArray = require( '@stdlib/assert-is-array' );
* var HasInstanceSymbol = require( '@stdlib/symbol-has-instance' );
*
* function ArrayLike() {
*     return {
*         'length': 3,
*         '0': 1,
*         '1': 2,
*         '2': 3
*     };
* };
*
* ArrayLike[ HasInstanceSymbol ] = isArray;
*/

// MAIN //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
